import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CountDown from 'views/mobileView/components/atoms/CountDown';

export default function PinOtpScreen(props){
    const [otp,setOtp]=React.useState('');
    const [invalidOtp,setInvalidOtp]=React.useState();
    const [nextDisabled,setNextDisabled]=React.useState(false)
    React.useEffect(() => {
        if(!props.location.state?.type && props.location.state.type !== 'new'){
			props.history.push('/menu')
		}
	}, []);
    React.useEffect(() => {
		checkValidation();
	}, [otp]);
    const checkValidation = () => {
            if (otp && otp.length === 6 ) {
                setNextDisabled(false);
            } else {
                setNextDisabled(true);
            }  
    };

    const resendOTP = () => {

    }

    return (
        <div className='mainContainer'>
            <MainHeader
                header='Set MF transaction PIN'
                navigation={props.history}
            />
            <div className="dataContainer" style={{paddingTop:'42px'}}>
                <div className="bigHeader">Let’s help you set a new PIN!</div>
                <div className="bodyTextDark" style={{ marginTop: "6px" }}>Please enter the OTP received on 999*****99 and sa***ak@mintbox.ai</div>
				<FloatingTextInput
					type="tel"
					id="typepass"
					label="OTP"
					style={{ letterSpacing: '4px' }}
					autoFocus
					onChange={(e) => {
						setInvalidOtp(false)
						const n = e.target.value.replace(/\D/g, '')
						setOtp(n)
					}}
					value={otp}
					maxLength={6}
				/>
				{invalidOtp ? (
					<div className="errorMsg">
						Incorrect OTP. Try again!
					</div>
				) : ''}
				<CountDown totalTime={30000} dir="down" onClick={() => { resendOTP() }} />
			</div>
            <div className='footer'>
                <button
                    className='solidButton'
                    style={{ opacity: nextDisabled ? 0.4 :1 }}
                    navigation={props.history}
                    onClick={()=>props.history.push({
                        pathname: '/menu/mf-transaction-pin/new-pin',
                        state : {...props.location.state}
                    })}
                    >
                    Confirm
                </button>
            </div>
        </div>
    )
}
